@import url(https://fonts.googleapis.com/css?family=Fira+Sans:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #f1f1f1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footer{height:50px}.contenedor{border-radius:6px;max-width:800px;padding:20px;margin:20px auto;margin-top:20px;background-color:rgba(4,124,204,.2);box-shadow:rgba(149,157,165,.2) 0px 8px 24px}input{width:100%;background-color:#fff;border:none;border-radius:6px;padding:6px;cursor:default;font-size:12pt;height:30px}input:focus{outline:none}.label{position:relative;top:-20px;padding-right:50px}
.App-header{background-color:#fff;min-height:70px;display:flex;flex-direction:column;justify-content:center;color:#fff}.App-header .container{margin:0 auto;max-width:800px;width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-between}.App-header .container div p{margin:0;color:#222;font-size:16pt;font-family:"Fira Sans"}.unlam-logo{height:32px;width:124px}
.codigo-container{display:flex;flex-direction:column;text-align:start}.codigo{border-radius:5px;height:300px;resize:vertical;max-height:50vh;border:none;outline:none}
.result{display:flex;flex-direction:column;background-color:#fefefe;border-radius:4px;padding:20px;transition:400ms ease-in-out}.result .result-value-container{font-size:48px;font-family:"Fira Sans";color:#047ccc}.result .result-description-container{font-family:"Fira Sans";font-weight:300}.result:hover{box-shadow:rgba(0,0,0,.24) 0px 3px 8px;transition:400ms ease-in-out;cursor:default}
.result-title-container p{font-weight:500;font-family:"Fira Sans";font-size:20pt;margin:0;margin-bottom:20px}
.results-container{margin-top:30px}.cards-container{margin:0 auto;display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:10px;grid-auto-rows:auto}@media only screen and (max-width: 600px){.cards-container{margin:0 auto;display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:10px;grid-auto-rows:auto}}
.submit-button{padding:15px 50px;border-radius:40px;border:none;background-color:#047ccc;color:#fff;transition:250ms ease-in-out;text-transform:uppercase;font-size:14pt;font-weight:300;letter-spacing:2px;font-family:"Fira Sans";cursor:pointer}.submit-button:hover{box-shadow:2px 3px 10px rgba(0,0,0,.473);transition:250ms ease-in-out}
.div-hide{-webkit-transform:scaleY(0);transform:scaleY(0);transition:height 400ms ease 0ms,-webkit-transform 400ms ease 0ms;transition:transform 400ms ease 0ms,height 400ms ease 0ms;transition:transform 400ms ease 0ms,height 400ms ease 0ms,-webkit-transform 400ms ease 0ms;height:0px}.div-show{-webkit-transform:scaleY(1);transform:scaleY(1);transition:height 400ms ease 0ms,-webkit-transform 400ms ease 0ms;transition:transform 400ms ease 0ms,height 400ms ease 0ms;transition:transform 400ms ease 0ms,height 400ms ease 0ms,-webkit-transform 400ms ease 0ms;height:100%}.unchecked-button{margin-top:5px;width:80px;height:30px;background-color:#047ccc;color:#fff;border:none;margin-right:5px}.checked-button{margin-top:5px;width:80px;height:30px;background-color:#fff;color:#047ccc;border:none;margin-right:5px}
